"use client";

import { useRef, useState, useEffect } from "react";
import { motion, useInView } from "framer-motion";
import { Goal, Trophy, Medal } from "lucide-react";
import { cn } from "@/lib/utils";
import { useTeamData } from "@/components/providers/team-provider";

interface TopScorer {
  jugador_id: number;
  nombre: string;
  posicion: string;
  dorsal?: number;
  total_goles: number;
  partidos: number;
}

export function TopScorersSection() {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true });
  const { topGoleadores: scorers, loading } = useTeamData();

  const getMedalIcon = (position: number) => {
    switch (position) {
      case 0:
        return <Medal className="h-5 w-5 text-yellow-500" />;
      case 1:
        return <Medal className="h-5 w-5 text-gray-400" />;
      case 2:
        return <Medal className="h-5 w-5 text-orange-600" />;
      default:
        return null;
    }
  };

  return (
    <section id="goleadores" className="bg-background py-24 lg:py-32" ref={ref}>
      <div className="mx-auto max-w-7xl px-4 lg:px-8">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.6 }}
          className="text-center"
        >
          <div className="mx-auto mb-3 inline-flex items-center gap-2 rounded-full bg-accent/10 border border-accent/20 px-4 py-1.5">
            <Goal className="h-4 w-4 text-accent" />
            <span className="text-sm font-semibold text-accent">
              MÃ¡ximos Anotadores
            </span>
          </div>
          <h2 className="text-3xl font-bold tracking-tight text-foreground sm:text-4xl lg:text-5xl text-balance">
            Tabla de Goleadores
          </h2>
          <p className="mx-auto mt-4 max-w-2xl text-lg text-muted-foreground">
            Nuestros mÃ¡ximos goleadores de la temporada.
          </p>
        </motion.div>

        {loading ? (
          <div className="text-center mt-12 text-muted-foreground">
            Cargando goleadores...
          </div>
        ) : scorers.length === 0 ? (
          <div className="text-center mt-12 text-muted-foreground">
            No hay goleadores registrados aÃºn.
          </div>
        ) : (
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={isInView ? { opacity: 1, y: 0 } : {}}
            transition={{ duration: 0.6, delay: 0.2 }}
            className="mt-12"
          >
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b border-border">
                    <th className="text-left py-2 px-2 sm:px-4 text-xs sm:text-sm font-semibold text-muted-foreground w-12">#</th>
                    <th className="text-left py-2 px-2 sm:px-4 text-xs sm:text-sm font-semibold text-muted-foreground">Jugador</th>
                    <th className="hidden sm:table-cell text-left py-2 px-2 sm:px-4 text-xs sm:text-sm font-semibold text-muted-foreground">PosiciÃ³n</th>
                    <th className="text-center py-2 px-2 sm:px-4 text-xs sm:text-sm font-semibold text-muted-foreground w-16 sm:w-20">Dorsal</th>
                    <th className="text-center py-2 px-2 sm:px-4 text-xs sm:text-sm font-semibold text-muted-foreground w-16 sm:w-20">Goles</th>
                  </tr>
                </thead>
                <tbody>
                  {scorers.map((scorer: any, index: number) => (
                    <motion.tr
                      key={scorer.id}
                      initial={{ opacity: 0, y: 10 }}
                      animate={isInView ? { opacity: 1, y: 0 } : {}}
                      transition={{ duration: 0.3, delay: index * 0.05 }}
                      className={cn(
                        "border-b border-border/50 transition-colors hover:bg-secondary/50",
                        index < 3 && "bg-accent/5"
                      )}
                    >
                      <td className="py-2 px-2 sm:px-4">
                        <div className="flex items-center gap-1 sm:gap-2">
                          {getMedalIcon(index)}
                          <span className="text-xs sm:text-sm font-semibold text-foreground">
                            {index + 1}
                          </span>
                        </div>
                      </td>
                      <td className="py-2 px-2 sm:px-4">
                        <span className="text-xs sm:text-sm font-semibold text-foreground truncate max-w-[120px] sm:max-w-[200px] block">
                          {scorer.nombre}
                        </span>
                      </td>
                      <td className="hidden sm:table-cell py-2 px-2 sm:px-4">
                        <span className="text-xs sm:text-sm text-muted-foreground">
                          {scorer.posicion}
                        </span>
                      </td>
                      <td className="py-2 px-2 sm:px-4 text-center">
                        <span className="inline-flex items-center justify-center h-6 w-6 sm:h-7 sm:w-7 rounded-full bg-accent/10 text-[10px] sm:text-xs font-bold text-accent">
                          {scorer.dorsal || '-'}
                        </span>
                      </td>
                      <td className="py-2 px-2 sm:px-4 text-center">
                        <span className="text-base sm:text-lg font-bold text-accent">
                          {scorer.goles_totales}
                        </span>
                      </td>
                    </motion.tr>
                  ))}
                </tbody>
              </table>
            </div>
          </motion.div>
        )}
      </div>
    </section>
  );
}
